import Vue from 'vue'
import Router from 'vue-router'
import Login from '@/components/login'
import Request from '@/components/request'

Vue.use(Router);


export default new Router({
  routes: [
    {
      path: '/',
      name: 'login',
      component: resolve => require(['../pages/userlog.vue'], resolve),
      children:[
        { path: '/login', component: Login},
        { path: '/request', component: Request}
      ]
    },
    {
      path: '/home',
      name: 'home',
      component: resolve => require(['../pages/Home.vue'], resolve),
    },
    {
      path: '/index',
      name: 'shouye',
      component: resolve => require(['../pages/Shouye.vue'], resolve),
    },
    {
      path: '/store',
      name: 'dingdan',
      component: resolve => require(['../pages/Dingdan.vue'], resolve),
    },
    {
      path: '/search',
      name: 'Chaxun',
      component: resolve => require(['../pages/Chaxun.vue'], resolve),
    },
    {
      path: '/detail/:id',
      name: 'detail',
      component: resolve => require(['../pages/Detail.vue'], resolve),
    },
    {
      path: '/wuliu/:id',
      name: 'wuliu',
      component: resolve => require(['../pages/wuliu.vue'], resolve),
    },
    {
      path: '/contrct/:id',
      name: 'contrct',
      component: resolve => require(['../pages/contrct.vue'], resolve),
    },
  ]
})





